@import url("https://fonts.googleapis.com/css2?family=Rubik+Glitch&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  background: #000;
  background-image: url(../img/8.webp);
  font-family: "Rubik Glitch", cursive;
}

.home {
  overflow: hidden;
}

/* menu */
nav {
  position: fixed !important;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  z-index: 2;
}

.menu__logo,
.menu__items-link {
  padding: 40px;
}

.menu__logo a {
  color: #fff;
  font-size: 22px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1px;
}

.menu__items {
  display: flex;
}

.menu__items-link a {
  color: #fff;
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.menu__items-link.active a {
  text-decoration: none;
}

/* horizontal scroll items  */
.container {
  margin-top: 200px;
  margin-left: 200px;
}

.container__item {
  position: relative;
  width: 540px;
  height: 360px;
  margin: 50px 500px 20px 80px;
}

.container__item-header h2 {
  position: relative;
  top: 40px;
  left: 0;
  right: 0;
  width: 600px;
  height: 360px;
  font-size: 64px !important;
}

.container__item h2 {
  position: absolute;
  left: 40px;
  bottom: -40px;
  color: #fff;
  font-weight: lighter;
  font-size: 64px;
  text-transform: lowercase;
}

.container__item-title a {
  color: #fff;
  text-decoration: none;
  text-shadow: -2px -2px 2px #222;
}

.container__item-1 {
  background: url(../img/1.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-2 {
  background: url(../img/2.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-3 {
  background: url(../img/3.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-4 {
  background: url(../img/4.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-5 {
  background: url(../img/5.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-6 {
  background: url(../img/6.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-7 {
  background: url(../img/9.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-8 {
  background: url(../img/10.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-9 {
  background: url(../img/11.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-10 {
  background: url(../img/12.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-11 {
  background: url(../img/13.webp) no-repeat 50% 50%;
  background-size: cover;
}

.container__item-12 {
  background: url(../img/14.webp) no-repeat 50% 50%;
  background-size: cover;
}

@media (max-width: 900px) {
  .container__item {
    width: 300px;
    height: 200px;
    margin: 50px 100px 20px 80px;
  }

  .container__item-header h2 {
    font-size: 30px !important;
    left: -50%;
  }

  .container__item h2 {
    font-size: 40px;
    bottom: -20px;
  }
}
@media (max-width: 600px) {
  .menu__items-link,
.menu__logo {
    padding: 40px 10px;
  }

  .menu__logo a {
    font-size: 14px;
  }

  .menu__items-link a {
    font-size: 14px;
  }

  .container__item-header h2 {
    left: -90%;
  }
}
/* about page styles */
#js-scroll {
  height: 200%;
}

.about__container {
  width: 50%;
  margin: 180px 0 100px 100px;
  padding: 40px;
  /* border: 1px solid #525252; */
}

.about__container p {
  margin: 60px 0;
  color: #fff;
  font-size: 46px;
  font-weight: 400;
  z-index: 2;
}

.about__container-contact-link a {
  color: #fff;
}

p.about__container-link {
  font-size: 26px;
}

.about__container-link a {
  color: #fff;
  font-size: 26px;
}

.cursor-follow {
  position: absolute;
  background: url(../img/7.webp) no-repeat 50% 50%;
  background-size: cover;
  width: 300px;
  height: 400px;
  z-index: -1;
  user-select: none;
  pointer-events: none;
  transform: translate(5px, 5px);
}

@media (max-width: 900px) {
  .about__container {
    width: 70%;
    margin: 140px 0 60px 40px;
  }

  .cursor-follow {
    display: none;
  }
}
@media (max-width: 900px) and (max-width: 600px) {
  .about__container {
    width: 95%;
    margin: 140px 0 60px 0px;
    padding: 40px 10px;
  }

  .about__container p {
    font-size: 32px;
  }
}
/* project section */
.project__container {
  width: 70%;
  margin: 100px auto;
  /* border: 1px solid #5c5c5c; */
  color: #fff;
  padding: 120px 40px;
  text-align: center;
}

.project__container-title h2 {
  color: #fff;
  font-size: 84px;
}

.project__container-subtitle p {
  margin: 20px 0;
  font-size: 30px;
}

.project__container-images {
  position: relative;
  width: 100%;
  height: 2200px;
  padding: 40px 0;
}

.project__image {
  background: red;
  width: 600px;
  height: 400px;
  margin: 40px auto;
}

.project__image-one {
  background: url(../img/1.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project__image-two {
  position: relative;
  top: -100px;
  left: -260px;
  background: url(../img/2.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project__image-three {
  position: relative;
  left: 300px;
  background: url(../img/3.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project__image-four {
  position: relative;
  top: -200px;
  left: -100px;
  background: url(../img/4.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project__image-five {
  position: relative;
  top: -220px;
  background: url(../img/5.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project__image-six {
  position: relative;
  top: -420px;
  left: -260px;
  background: url(../img/6.webp) no-repeat 50% 50%;
  background-size: cover;
}

.project-next-link {
  position: relative;
  top: -100px;
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.project-next-link a {
  color: #fff;
  text-decoration: none;
  font-size: 160px;
}

.anime-js-title .letter {
  display: inline-block;
  line-height: 1em;
}

@media (max-width: 900px) {
  .project__container-title h2 {
    font-size: 40px;
  }

  .project__container {
    width: 80%;
  }

  .project__container-images {
    width: 100%;
    margin: 0 auto;
    height: 2600px;
  }

  .project__image {
    width: 90%;
  }

  .project__image-one,
.project__image-two,
.project__image-three,
.project__image-four,
.project__image-five,
.project__image-six {
    position: relative;
    top: 0;
    left: 0;
  }
}
@media (max-width: 600px) {
  .project__container {
    width: 95%;
    padding: 120px 0px;
  }

  .project-next-link a {
    font-size: 84px;
  }
}